@font-face {
    font-family: 'Proxima Nova Semibold';
    src: url('../font/ProximaNova-Semibold.eot');
    src: url('../font/ProximaNova-Semibold.eot?#iefix') format('embedded-opentype'),
    url('../font/ProximaNova-Semibold.woff') format('woff'),
    url('../font/ProximaNova-Semibold.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Proxima Nova';
    src: url('../font/ProximaNova-Regular.eot');
    src: url('../font/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
    url('../font/ProximaNova-Regular.woff') format('woff'),
    url('../font/ProximaNova-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Proxima Nova Light';
    src: url('../font/ProximaNova-Light.eot');
    src: url('../font/ProximaNova-Light.eot?#iefix') format('embedded-opentype'),
    url('../font/ProximaNova-Light.woff') format('woff'),
    url('../font/ProximaNova-Light.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'ProximaNova Bold';
    src: url('../font/ProximaNova-Bold.eot');
    src: url('../font/ProximaNova-Bold.eot?#iefix') format('embedded-opentype'),
    url('../font/ProximaNova-Bold.woff') format('woff'),
    url('../font/ProximaNova-Bold.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

$fontReg: 'Proxima Nova';
$fontRegLight: 'Proxima Nova Light';
$fontRegBold: 'ProximaNova Bold';
$fontSemi: 'Proxima Nova Semibold';

$baseColorDark: #9071ea;
$baseColor1:#5e42b0;
$baseColor1A: rgba(94, 66, 176, 0.06);
$baseColor2: #FFF; 
$baseColor2A: rgba(255, 255, 255, 0.3); 
$baseColor3: #9173e6;
$baseColor4: #a496ca;
$baseColorCardTitle: #e7e7e7;
$baseColorCardTitle2: #c1c1c1;
$baseColorCardTitle2A: rgba(193, 193, 193, 0.4);
$baseColorDigit: #f5f6f7;
$baseColorBlack: #000000;
$baseColorShadow: #0a0e34;
$baseColorShadowA: rgba(10, 14, 52, 0.04);
$baseColorShadowA60: rgba(10, 14, 52, 0.6);
$baseColorSlider: #d8d8d8;
$baseColorSliderSelect: #5c3caf;



*{
    margin: 0px;
    padding: 0px;
}

.width-adapt {
    width: 1530/1920*100%;
    margin: 0 auto;
}

// заголовки самые крупные ============================
.title__line1,
.title__line2 {
    font-family: $fontRegBold;
    font-size: 90px;
    line-height: 90px;
    text-transform: uppercase;
    letter-spacing: 3.5px;
    color: $baseColor2;
}

// фиолетовые большые кнопки ========================
.button__container {
    margin-top: 181px;
    display: flex;
    justify-content: center;


        
    .button {
        display: flex;
        align-items: center;
        justify-content: center;
        
        width: 311px;
        border-radius: 13%/50%;
        text-decoration: none;
        color: $baseColor2;
        margin: 0 25px;
        padding: 20px 0;
        box-shadow: 0 16px 24px $baseColorShadow;
        background-color: $baseColor1;
        transition: border-radius 0.15s cubic-bezier(1, 0, 0, 1);


        &__text {
            padding-left: 28px;
            text-transform: uppercase;
        }
        
        .line-text:last-child {
            font-family: $fontSemi;
            font-weight: 400;
        }

        .line-text {
            line-height: 15px;
        }

        &:hover {
            border-radius: 10px;
            color: $baseColor2;
            background-color: $baseColor3;            
        }
    }
}

.appStore::before {
    content: "";
    display: flex;
    background: url(../img/icon-Apple.png) 
    no-repeat center center/contain;
    width: 31px;
    height: 37px;
}

.googlePlay::before {
    content: "";
    display: flex;
    background: url(../img/icon-GooglePlay.png) 
    no-repeat center center/contain;
    width: 36px;
    height: 39px;
}

//  меню и средний текст ===========
.header-small-text {
    font-family: $fontRegLight;
    font-size: 14px;
    font-weight: 300;
    line-height: 11px;
    text-transform: uppercase;
    letter-spacing: 0.7px;
}


//  хедер =========================================================
.header {
    width: 100%;
    background: url(../img/1500/football-top.jpg) no-repeat center top/cover;


    &>.container {
        padding: 54px 0 110px 0;
        display: flex;
        align-self: center;
        justify-content: center;
        flex-flow: column nowrap;

        .top-block {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;

            &__logotype {
                background: url(../img/logotype.svg) no-repeat center top/cover;
                width: 189px;
                height: 33px;
            }
            
            &__slogan {
                color: $baseColor2;
            }
        }
    
        .link {
            text-decoration: none;
            color: $baseColor1;
        }
    
        .link:hover {
            color: $baseColor2;
        }
        
        .title-container {
            display: flex;
            flex-flow: column wrap;
            
            .title__line1 {
                margin-top: 309px;
                color: $baseColor2;
                align-self: flex-start;
                z-index: 10;
            }
    
            .title__line2 {
                color: $baseColor2;
                display: flex;
                align-self: flex-end;
                position: relative;
            }
    
            .title__line2::before {
                content: "";
                background: url(../img/line_violet_top.svg) 
                no-repeat center top/contain;
                width: 254px;
                height: 317px;
                position: absolute;
                left: -62px;
                bottom: -55px;
            }
        }
        
        .menu {
            display: flex;
            justify-content: flex-end;
            margin-top: 30px;

            &__link {
                margin-left: 3.43em;
                text-decoration: none;
                color: $baseColorDark;

                &:first-child {
                    margin-left: 0;
                }
            }

            &__link:hover{
                color: $baseColor2;
            }
        }
    }
}

//  спортсмены =======================================
.overflow-pic {
    overflow: hidden;
}

.block1 {
    .section {
        padding: 112px 0 34px 0;
        display: flex;
        justify-content: center;


        .container {
            width: 1531 / 1920 * 100%;
            display: flex;
            justify-content: flex-end;

            .title {
                width: 714 / 1530 * 100%;
                position: relative;
                color: $baseColorCardTitle;
                font-family: $fontRegBold;
                font-size: 90px;
                font-weight: 700;
                line-height: 63px;
                text-transform: uppercase;

                &::after {
                    content: "";
                    background: url(../img/1500/footballer.png)
                                no-repeat top right/contain;
                    position: absolute;
                    width: 725px;
                    height:1105px;
                    right: 100%;
                    top: -86px;

                }
            }            
        }
    }
    .card {
        width: 100%;
        padding: 60px 0;
        display: flex;
        justify-content: center;

        .container {
            width: 1531 / 1920 * 100%;
            display: flex;
            justify-content: flex-end;

            .card-content {
                width: 714 / 1530 * 100%;
                color: $baseColorBlack;
                font-family: $fontReg;
                font-size: 20px;
                font-weight: 400;
                line-height: 25px;
                letter-spacing: 1px;
            }

            .white-color {
                color: $baseColor2;
            }
        }

        
        &:nth-child(3){
           background-color: $baseColor1A;
        }

        &:nth-child(4){
            background-color: $baseColor1;
            padding: 95px 0;
        }

        &__title {
            color: $baseColorCardTitle2;
            font-family: $fontRegBold;
            font-size: 36px;
            font-weight: 700;
            line-height: 26px;
            text-transform: uppercase;
            letter-spacing: 1.8px;
            margin-bottom: 31px;

            display: flex;
            align-items: center;

            .card__digit {
                color: $baseColorBlack;
                font-family: $fontRegBold;
                font-size: 18px;
                line-height: 26px;
                text-transform: uppercase;
                letter-spacing: 0.9px;
                margin-right: 12px;

                .design-el {
                    width: 22px;
                    height: 5px;
                    border-radius: 2px;
                    background-color: $baseColor1;
                }
                .gray {
                    background-color: $baseColorCardTitle;
                }
            }

            .violet {
                color: $baseColor3;
            }
        }

        .slider-nav {
            display: none;
            overflow: hidden;
        }
    }
}

.block2 {
    .col-1-lm {
        margin-left: 131px;
    }

    .section {
    padding: 112px 0 34px 0;

        .container {
            justify-content: flex-start;

            .title {

                &::after{
                    content: "";
                    background: url(../img/1500/basketboller.png)
                                no-repeat top left/cover;
                    position: absolute;
                    width: 991px;
                    height:815px;
                    left: 480px;
                    top: -141px;
                }
            }
        }  
    } 
    .card {
        .container {
            width: calc( 1531 / 1920 * 100%);
            margin: 0 auto;
            display: flex;
            justify-content: flex-start;

            .card-content {
                width: calc(600 / 1530 * 100%);
            }
        }

        &:nth-child(3){
           background-color: $baseColorShadowA;
        }

        &:nth-child(4){
            background-color: $baseColorShadow;
            padding: 95px 0;
        }
    }
}

// review block =======================================
.review-block {
    display: flex;
    flex-flow: column wrap;

    .card {
        width: 100%;
        display: flex;
        flex-flow: row nowrap;
        position: relative;
        min-height: 730px;

        .pic {
            min-width: 50%;
            min-height: 730px;
            background-color: gray;
        }
        .pic1 {
            background: url(../img/1500/timeman.jpg) no-repeat top center/cover;
        }
        .pic2 {
            background: url(../img/1500/dictor.jpg) no-repeat top left/cover;
        }
        .pic3 {
            background: url(../img/1500/win-command.jpg) no-repeat top center/cover;
        }

        .text-container {
            width: 50%;
            padding: 0px 0px 122px 148px;
            align-self: flex-start;

            .title {
                max-width: 100%;
                margin-top: 257px;
                padding-right: 90px;
                color: $baseColorCardTitle2A;
                font-family: $fontRegBold;
                font-size: 90px;
                font-weight: 700;
                line-height: 1.5em;
                text-transform: uppercase;
                position: relative;

                &::first-line {
                    line-height: 63px;
                }
            }
            

            .content {
                width: 500 / 960 *100%;
                color: $baseColorBlack;
                font-family: $fontReg;
                font-size: 24px;
                font-weight: 400;
                line-height: 28px;
                letter-spacing: 1.2px;
                margin-top: 119px; 
            }

           .content::after {
                content: "";
                position: relative;
                left: 0;
                display: block;
                width: 22px;
                height: 5px;
                border-radius: 2px;
                background-color: $baseColor1;
                margin-top: 87px;
            }
        }
        
        .digit-line-block {
            background-color: $baseColor1;
            width: 168px;
            height: 155px;
            position: absolute;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            top: -47px;
            left: -150px;
            transform: translateX(-50%);

            .digit-text {
                font-family: $fontRegBold;
                font-size: 24px;
                font-weight: 700;
                line-height: 18px;
                text-transform: uppercase;
                letter-spacing: 1.2px;
                color: $baseColorDigit;
                display: flex;
                flex-direction: column;
                align-items: center;
            }

            .bottom-line {
                margin-top: 9px;
                display: block;
                width: 22px;
                height: 5px;
                border-radius: 2px;
                background-color: $baseColor2A;
            }
        }
    }

    .right-pic {
        flex-direction: row-reverse;

        .text-container {
            justify-content: flex-end;

            .title {

                .digit-line-block {
                    left: 100%;
                    transform: translateX(-50%);
                }
            }
        }
    }
}

//  footer =======================================
.footer {
    background: url(../img/1500/stadium-bottom.jpg) no-repeat bottom center/cover;
    display: flex;
    align-items: center;
    flex-direction: column;

    .download-container {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 209px;

        .btn-container {
            background: url(../img/logo-btn.png) no-repeat bottom center/cover;
            width: 130px;
            height: 137px;
            margin-right: 36px;
        }

        .text-container {
            cursor: pointer;   
            position: relative;   
                        z-index: 2;
        }

        .text-container::before {
            content: "";
            background: url(../img/line2_violet_bottom_1920.svg) 
                        no-repeat center center/contain;
            width: 612px;
            height: 314px;
            left: 24px;
            top: 50%;
            transform: translateY(-50%);
            position: absolute;
            z-index: -1;
        }

        .title__line1 {
            z-index: 10;
        }

        .title__subline1 {
            color: $baseColor4;
            font-family: $fontReg;
            font-size: 36px;
            font-weight: 700;
            line-height: 26px;
            text-transform: uppercase;
            z-index: 10;
        }
    }

    .button__container {
        margin: 151px 0 197px 0;
    }

    .bottom-container {
        width: 100%;
        padding: 69px 0px 69px 0px;
        background-color: $baseColorShadowA60;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;

        .logotype {
            margin-left: 55px;
            background: url(../img/logotype.svg) no-repeat center center/cover;
            width: 172px;
            height: 30px;
        }

        .icon-social {
            display: flex;
            flex-direction: row;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            height: 27px;
            
            .icon {
                display: flex;
                margin-right: 40px;
                align-self: center;

                &:last-child {
                    margin-right: 0px;
                }
            }

        }

        .contact-text {
            text-decoration: none;
            margin-right: 55px;
            color: $baseColor2;
            font-family: $fontReg;
            font-size: 20px;
            line-height: 15px;
            letter-spacing: 1px;
        }
    }
}


@media screen and (min-width: 1921px){
    .review-block {
        .right-pic {
            .text-container {
                .title {
                    max-width: 100%;
                }
            }
        }   
    }
}


@media screen and (max-width: 1780px) {

    .header {
        
        .header-small-text {
            font-size: 14px;
            line-height: 14px;
        }
        .container {
            padding: 52px 0 91px 0;
             
    
            .title-container {
                display: flex;
                align-self: flex-end;
    
                .title__line1 {
                    margin-left: 6%;
                    z-index: 10;
                }
    
                .title__line2 {
                    margin-left: 21%;
                    position: relative;
                    margin-bottom: 30px;
                    z-index: 9;
                }
    
                .title__line2::before {
                    content: "";
                    background: url(../img/line_violet_top.svg) 
                    no-repeat center top/contain;
                    width: 254px;
                    height: 317px;
                    position: absolute;
                    left: -34px;
                    bottom: -24px;
                    z-index: -1;
                }
            }

            .menu {
                display: flex;
                justify-content: center;
                margin-top: 121px;        
            }

            .button__container {
                margin-top: 116px;

                .button {
    
                    &__text {
                        padding-left: 28px;
                    }
                    
                    .line-text:last-child {
                        font-family: $fontSemi;
                        font-weight: 400;
                    }
    
                    .line-text {
                        line-height: 15px;
                    }
                }
            }            
        }
    }

    .block2 {
        .col-1-lm {
            margin-left: 0px;
        }
        .section {
            .container {
                .title {                
                    &::after{
                        left: 400px;
                    }
                }
            }
        }
    }
}

