                // margin-left: 55px;
                // background: url(../img/logotype.svg) no-repeat center center/cover;
                // width: 172px;
                // height: 30px;
            }

            .icon-social {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                height: 27px;
                
                .icon {
                    margin-right: 40px;
                    align-self: center;

                    &:last-child {
                        margin-right: 0px;
                    }
                }

            }

            .contact-text {
                display: none;
                // margin-right: 55px;
                // color: $baseColor2;
                // font-family: $fontReg;
                // font-size: 20px;
                // line-height: 15px;
                // letter-spacing: 1px;
            }
        }
    }
}

// 
// можно так же особо для шапки и заголовков вставить переходную точку с 500 до 767
// пока же делаем сразу до 767 // еще доработать менб в шапке

@media screen and (max-width: 767px) {

    .width-adapt {
        width: 297/320*100%;
        margin: 0 auto;
    }

    .title__line1,
    .title__line2 {
            font-size: 35px;
            line-height: 0.979;
            letter-spacing: 1.5px;
    }

    .appStore::before {
        width: 15px;
        height: 17px;
    }

    .googlePlay::before {
            width: 16px;
            height: 19px;
    }

    .header {

        
        .header-small-text {
            font-size: 12px;
            line-height: 9px;
        }
        .container {
            padding: 39px 0 20px 0;

             

            .top-block {
                width: 100%;
                display: flex;
                justify-content: center ;
                flex-flow: column;
    
                &__logotype {
                    // background: url(../img/logotype.svg) no-repeat center top/contain;
                    width: 137px;
                    height: 24px;
                }
                
                &__slogan {
                    font-size: 0;
                    height: 0;
                    overflow: hidden;
                    visibility: hidden;
                }
            }
    
            .title-container {
                display: flex;
                align-self: center;

                .title__line1,
                .title__line2 {
                    //  уеличит размер шрифта при ширине 500++ ==================
                    font-size: 32px; 
                    font-weight: bold;
                    line-height: 1.2;
                }
    
                .title__line1 {
                    margin-top: 141px;
                    margin-left: 0%;
                }
    
                .title__line2 {
                    margin-left: 18%;
                    margin-bottom: 0px;
                    max-width: 310px;   
                }
    
                .title__line2::before {
                    width: 141px;
                    height: 175px;
                    left: 22px;
                    bottom: -18px;
                }
            }

            .menu {
                max-width: 290px;
                order: 1;
                display: flex;
                flex-flow: wrap;
                align-self: center;
                margin-top: 45px;

                &__link {
                    margin-left: 2.36em;
                }

                &__link:nth-child(5n-1) {
                    margin-left: 0;
                }

                &__link:nth-child(5n-1),
                &__link:last-child {
                    margin-top: 12px; 
                }
            }

            .button__container {
                margin-top: 115px;
                display: flex;
                justify-content: center;
                align-items: center;
                
                .button {
                    width: 142px;
                    height: 39px;
                    margin: 0 4px;
                    padding: 0px;
                    box-shadow: 0px 10px 16px 0px $baseColorShadow;
    
                    &__text {
                        padding-left: 9px;
                        font-size: 8px;
                        line-height: 7px;
                    }
                    
                    .line-text:last-child {
                        font-family: $fontSemi;
                    }
    
                    .line-text {
                        line-height: 8px;
                        font-family: $fontRegLight;
                        font-size: 8px;
                        text-transform: uppercase;
                        line-height: 1.2;
                        text-align: left;
                    }
                }
            }
        }
    }

    .block1 {
        display: flex;
        flex-flow: row wrap;
        
        .section {
            z-index: 10;
            padding: 26px 0 235px 0;
            flex: 1 0 100%;

            .container {
                width: 288 / 320 * 100%;


                .title {
                    width: 100%;
                    position: relative;
                    font-size: 52px;
                    font-weight: 700;
                    line-height: 55px;
                    text-transform: uppercase;
                    text-align: left;

                    &::after {
                        background: url(../img/320/footballer.png)
                                    no-repeat top right/cover;
                        width: 211px;
                        height:281px;
                        left: 40px;
                        top: 65px;
                        z-index: 100;
                    }
                }            
            }
        }        

        .card {
            width: 100%;
            padding: 83px 0 27px 0;
            display: flex;
            align-items: center;
            flex-direction: column;
            justify-content: space-between;

            .container {
                width: 288 / 320 * 100%;
                
                .card-content {
                    width: 100%;
                    font-size: 15px;
                    line-height: 18px;
                    text-align: left;
                }

                .white-color {
                    color: $baseColorBlack;
                }
            }
            
            &:nth-child(2){
                background-color: $baseColor1A;
                display: none;
            }

            &:nth-child(3){
                background-color: $baseColor1A;
            }

            &:nth-child(4){
                background-color: $baseColor1A;
                display: none;
            }

            &__title {
                color: $baseColorCardTitle2;
                font-family: $fontRegBold;
                font-size: 28px;
                font-weight: 700;
                line-height: 19px;
                text-transform: uppercase;
                letter-spacing: 1.8px;
                margin-bottom: 27px;
                display: flex;
                align-items: center;

                .card__digit {
                    color: $baseColorBlack;
                    font-family: $fontRegBold;
                    font-size: 14px;
                    line-height: 20px;
                    text-transform: uppercase;
                    letter-spacing: 0.9px;
                    margin-right: 12px;

                    .design-el {
                        width: 17px;
                        height: 4px;
                        border-radius: 2px;
                        background-color: $baseColor1;
                    }
                    .gray {
                        background-color: $baseColor1;
                    }
                }

                .violet {
                    color: $baseColor3;
                }

            }

            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(4) {
                height: 185px;
                word-wrap: break-word;
                overflow: hidden;
            }

            .slider-nav {
                overflow: visible;
                display: flex;
                flex-direction: row;

                &__circle {
                    border-radius: 50%;
                    background-color: $baseColorSlider;
                    width: 10px;
                    height: 10px;
                    margin: 0 6px;

                    &:hover {
                        background-color: $baseColorSliderSelect;
                    }
                }
            }
        }
    }

    .block2 {

        .section {

            .container {

                .title {

                    &::after {
                        background: url(../img/320/basketboller.png)
                                    no-repeat top right/cover;
                        width: 302px;
                        height:250px;
                        left: -10px;
                        top: 75px;
                        z-index: 100;
                    }
                }            
            }
        }        

        .card {

            &:nth-child(n) {
                // перебиваем вес от 1080
                padding: 83px 0 27px 0;
            }
            
            &:nth-child(2){
                background-color: $baseColor1A;
                display: none;
            }

            &:nth-child(3){
                background-color: $baseColor1A;
                display: none;
            }

            &:nth-child(4){
                background-color: $baseColor1A;
                display: flex;
            }

            &__title {

                .violet {
                    color: $baseColorBlack;
                }
            }

            &:nth-child(2),
            &:nth-child(3),
            &:nth-child(4) {
                height: 132px;
            }
        }
    }

    .review-block {

        .card {
            flex-direction: column;
            min-height: 100%;

            .pic {
                padding-top: 351 / 320 * 100%; 
                min-height: 351px;
            }

            .digit-line-block {
                width: 70px;
                height: 70px;
                top: -93px;


                .digit-text {
                    font-size: 14px;
                    line-height: 18px;
                }

                .bottom-line {
                    margin-top: 5px;
                    width: 16px;
                    height: 4px;
                }
            }


            .text-container {
                padding: 0px 20px 51px 19px;
                width: 265 / 320 * 100%;

                .title {
                    margin-top: 36px;
                    max-width: 100%;
                    line-height: 28px;
                    font-size: 28px;
                    text-align: left;

                    &::first-line {
                        line-height: 28px;
                    }
                } 

                .content {
                    width: 100%;
                    margin-top: 24px; 
                    font-size: 16px;
                    line-height: 1.25;
                }

                .content::after {
                    margin-top: 23px;
                }
            }

        }

        .right-pic {

            .text-container {

                .title {
                    padding-right: 0px;

                    .digit-line-block {
                        left: 0;
                        transform: translateX(0);
                    }
                }
            }
        }
    }

    .footer {
        background: url(../img/320/stadium-bottom.jpg) no-repeat bottom center/cover;

        .download-container {
            padding-top: 97px;

            .btn-container {
                background: url(../img/logo-btn.png) no-repeat bottom center/cover;
                width: 74px;
                height: 79px;
            }

            .text-container {
                margin-top: 57px;
            }

            .text-container::before {
                width: 226px;
                height: 123px;
            }

            .title__line1 {
                font-size: 34px;
                line-height:24px;
                text-align: center;
                letter-spacing: 1px;
            }

            .title__subline1 {
                font-size: 15px;
                line-height: 11px;
                margin-top: 14px; 
            }
        }

        .button__container {
            margin: 73px 0 127px 0;

            .button {
                width: 142px;
                height: 39px;
                margin: 0 4px;
                padding: 0px;
                box-shadow: 0px 10px 16px 0px $baseColorShadow;

                &__text {
                    padding-left: 9px;
                    font-size: 8px;
                    line-height: 7px;
                }
                
                .line-text:last-child {
                    font-family: $fontSemi;
                }

                .line-text {
                    line-height: 8px;
                    font-family: $fontRegLight;
                    font-size: 8px;
                    text-transform: uppercase;
                    line-height: 1.2;
                    text-align: left;
                }
            }
        }

        .bottom-container {
            padding: 26px 0px;

            .icon-social {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
                
                .icon {
                    height: 18px;
                }

            }
        }
    }
}

